    font-family: 'nunito' ;
    --primary : #B6D94C;
    --secondary : #FFE873;
    --support : #87CEEB;
    --primarySoft:#E1FFB8; 
    --primaryHard : #65B741;
    --secondaryHard :gold;
    --black : #000000;
    --white : #FFFFFF;
    --cardColor : #F3F3F3;
    
}

*{
    box-sizing: content-box;
}

*:not(dialog, dialog[open]){
    padding:0;
    margin:0;
}

a{
    color: inherit;
    text-decoration: none;
}
a:visited{
    color: inherit;
    text-decoration: none;
}

body{
    background-color:#FFF8E1;
}

header#header{
    height:90vh;
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    position: relative;
    box-shadow: 0px 0px 10px 5px rgba(0, 0, 0, 1);
}

header > nav#nav{
    display: flex;
    height: 60px;
    align-items: center;
    justify-content: center;
    padding: 10px 0 0 100px;
    position: sticky;
    top: 0;
    right: 0;
    left: 0;
    z-index: 9999;
    user-select: none;
}

nav *{
    font-size: 1.15rem;
}

nav#nav > h3{
    color: var(--black);
    align-self: center;
    margin : 0 auto 0 0; 
    transition: .3s;
    cursor:pointer;
    border: solid 2px var(--black);
    padding:15px;
    background-color: var(--primary);
    border-radius: 15px 30px 15px 30px ;
    user-select: none;
    box-shadow: 0px 0px 10px 5px rgba(255, 255, 255, .25);
}

#nav > h3:hover{
    background-color: var(--primaryHard);
    color: var(--white);
    border: solid 2px var(--white);
}

#nav > h3:active{
    color:var(--black);
    background-color: var(--primary);
    border: 2px solid var(--black);
}

#nav > h3:hover{
    scale:1.1;
}
#nav > h3:active{
    scale:1
}

#nav > section{
    box-shadow: 0px 0px 10px 5px rgba(255, 255, 255, .1);
}

nav > section:first-of-type{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    border-radius:25px 0 0 15px ;
    background-color: var(--secondary);
}

nav > section:first-of-type button{
    height: 100%;
    padding:20px;
    border:none;
    cursor: pointer;
    transition:.15s;
    background-color: var(--secondary);
}

nav > section:last-child{
    height: 100%;
    display: flex;
    align-items: center;
    gap:20px;
    margin-right:70px;
    border-radius: 0px 15px 25px 0;
    background-color: var(--secondary);

}


nav  > section:last-child > button{
    padding:0 20px;
    height: 100%;
    background-color: rgb(246, 255, 227,0);

    border:none;

    
    cursor:pointer;
    font-weight: bold;
    transition:.15s;
    font-family: 'nunito';
}

nav > section:first-of-type > button:hover{
    background-color: var(--primary);
    transform: scale(1.3);
}

nav > section:last-child > button:hover{
    background-color: var(--primary);
    transform: scale(1.08);
}


nav#nav > section > button:active{
    transform: scale(0.95);
}

nav > section{
    background-color: white;
    overflow: hidden;
}

#cover{
    position: absolute;
    width: 100%;
    height: 100%;
    top:0;
    left:0;
    right:0;

    background-color: rgba(0, 0, 0, .45);
}

#cover-container{
    height: 90%;
    width: 650px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 0 0 0 50px;
    gap:10px;
    color: var(--primary);
}

#cover-container > h5 {
    font-size: 1.25rem;
}

#cover-container > h1{
    font-size: 3rem;
    color: var(--secondaryHard);
}

#cover-container > p{
    font-size:1.1rem;
    line-height: 1.7rem;
    color:var(--secondary);
}

#cover-container > a{
    margin:20px 0 0 30px;
    font-family: 'nunito';
    width:fit-content;
    padding:15px 30px;
    border:none;
    border-radius:5px;
    cursor:pointer;
    color: var(--black);
    background-color: var(--secondary);
    box-shadow: 0px 0px 5px 1px rgba(255, 255, 255, .45);
    font-size: 1.05rem;
    transition: .2s;
}

#cover-container > a::after{
    content :'ðŸ‘‰'
}

#cover-container > a:hover{
    background-color: black;
    color:gold;
    transform: scale(1.03);
    box-shadow:0px 35px 35px -18px rgba(255, 255, 255), 0px 0px 5px 1px rgba(255, 255, 255, .35);
}


#footer{
    background-color: var(--cardColor);
    height:30px;
    position: fixed;
    bottom:0;
    left:0;
    right:0;
    box-shadow: 0px 0px 5px 1px rgba(0, 0, 0, .2);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 99999999999;
}

#footer > h5{
    font-size: 1rem;
    user-select: none;
    transition: .2s; cursor: pointer;
}

#footer> h5:hover{
    color: var(--primaryHard);

}

#section2 > .active{
    background-color: var(--primaryHard);
